}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 112px;
    height: 34px;
  }
}

.footer__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 22px;
  padding: 0;
  margin: 0;
}
@media (max-width: 767px) {
  .footer__list {
    display: block;
    columns: auto 2;
  }
}

.footer__list-item {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}
@media (max-width: 767px) {
  .footer__list-item {
    padding-bottom: 15px;
  }
}

.footer__list-link:hover {
  border-bottom: 1px solid #2d383f;
}
.footer__list-link:focus {
  padding: 3px 5px;
  outline: 1px solid #2d383f;
  border: none;
}
.footer__list-link:active {
  border: none;
}
.footer__list-link:disabled {
  opacity: 0.5;
}

.footer__phone {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  color: #2d383f;
}
@media (max-width: 1199px) {
  .footer__phone {
    display: none;
  }
}
.footer__phone:hover, .footer__phone:focus {
  color: #1c3374;
  outline: none;
}
.footer__phone:active {
  color: #0266c1;
}
.footer__phone:disabled {
  opacity: 0.1;
}

.footer__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
@media (max-width: 767px) {
  .footer__bottom {
    align-items: flex-start;
  }
}

.footer__social {
  position: relative;
  width: 120px;
  height: 30px;
}

.footer__social-link {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #ffffff;
  border: 1px solid rgba(2, 102, 193, 0.1);
}
.footer__social-link--vk {
  top: 0;
  left: 0;
}
.footer__social-link--ok {
  left: 45px;
  top: 0;
}
.footer__social-link--reddit {
  top: 0;
  right: 0;
}

.footer__social-link:hover {
  border: 1px solid #2d383f;
}

.footer__social-link:active {
  background-color: #0266c1;
  border: 1px solid rgba(255, 255, 255, 0);
}

.footer__social-link:disabled {
  opacity: 0.3;
}

.footer__social-link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer__social-link:active svg {
  fill: #ffffff;
  color: #ffffff;
}

.footer__social-link:disabled svg {
  opacity: 0.3;
}

.footer__bottom-link {
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
}

